import React, { useState } from "react";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

const Search = () => {
  const [city, setCity] = useState("");
  const [countryCode, setCountryCode] = useState("");
  const handleSubmit = (e) =>{
      e.preventDefault();
      if(city && countryCode){
        console.log(city,countryCode)
      }
  }
  return (
    <div>
      <form noValidate autoComplete="off" className="searchForm" onSubmit={handleSubmit}>
        <TextField
          onChange={(e) => setCity(e.target.value)}
          variant="outlined"
          label="City"
          className="formItem"
          required
        />
        <TextField
          onChange={(e) => setCountryCode(e.target.value)}
          variant="outlined"
          label="Country Code"
          className="formItem"
        />
        <Button
          type="submit"
          variant="contained"
          color="primary"
          className="formItem"
        >
          Search
        </Button>
      </form>
    </div>
  );
};

export default Search;
